// frontend/components/ArticleCard.jsx
import Link from 'next/link';

export default function ArticleCard({ article }) {
  return (
    <article style={{ border:'1px solid #eee', padding:12, borderRadius:8, marginBottom:12 }}>
      <h3><Link href={`/articles/${article._id}`}>{article.title}</Link></h3>
      <p style={{ color:'#666' }}>{article.author} • {new Date(article.createdAt).toLocaleDateString()}</p>
      <p>{(article.content||'').slice(0,200)}{article.content && article.content.length>200 ? '...' : ''}</p>
    </article>
  )
}