// frontend/components/ProjectCard.jsx
import Link from 'next/link';

export default function ProjectCard({ project }) {
  return (
    <article style={{ border:'1px solid #eee', padding:12, borderRadius:8, marginBottom:12 }}>
      <h3><Link href={`/projects/${project._id}`}>{project.title}</Link></h3>
      <p style={{ color:'#666' }}>{project.link}</p>
      <p>{(project.description||'').slice(0,200)}{project.description && project.description.length>200 ? '...' : ''}</p>
    </article>
  )
}